<template>
  <swiper :lazy="true" :navigation="true"
          :style="{'--swiper-navigation-color': '#fff','--swiper-pagination-color': '#fff'}" :breakpoints="{
      '1200': {
        'slidesPerView': 1,
      }
    }" class="mySwiper h-100"
  >

    <swiper-slide v-for="slide in this.images[this.lang]" :key="slide.section">
      <img :src="slide.src" class="swiper-lazy vh-100" alt=""/>
      <div class="swiper-lazy-preloader swiper-lazy-preloader-white"></div>
    </swiper-slide>

  </swiper>
</template>
<script>
// Import Swiper Vue.js components
import {Swiper, SwiperSlide} from 'swiper/vue';

// Import Swiper styles
import 'swiper/swiper.scss';

import "swiper/components/lazy/lazy.min.css"
import "swiper/components/pagination/pagination.min.css"
import "swiper/components/navigation/navigation.min.css"

// import Swiper core and required modules
import SwiperCore, {
  Lazy, Pagination, Navigation
} from 'swiper/core';

// install Swiper modules
SwiperCore.use([Lazy, Pagination, Navigation]);

export default {
  props: ['images', 'lang'],
  components: {
    Swiper,
    SwiperSlide,
  },
  data() {

  },
  methods: {}

}
</script>
<style>
.swiper-button-next, .swiper-container-rtl .swiper-button-prev {
  right: 10px;
  left: auto;
}
.swiper-button-next.swiper-button-disabled, .swiper-button-prev.swiper-button-disabled {
  opacity: .35;
  cursor: auto;
  pointer-events: none;
}
.swiper-button-prev:after, .swiper-container-rtl  {
  content: 'prev';
}
.swiper-button-next:after{
  content: 'next';
}
.swiper-button-next:after, .swiper-button-prev:after {
  font-family: swiper-icons;
  font-size: var(--swiper-navigation-size);
  text-transform: none!important;
  letter-spacing: 0;
  text-transform: none;
  font-variant: initial;
  line-height: 1;
}
.swiper-button-next, .swiper-button-prev {
  position: absolute;
  top: 50%;
  width: calc(var(--swiper-navigation-size) / 44 * 27);
  height: var(--swiper-navigation-size);
  margin-top: calc(0px - (var(--swiper-navigation-size) / 2));
  z-index: 10;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--swiper-navigation-color, var(--swiper-theme-color));
  font-size: 10vw;
  font-weight: 900;
}

.swiper-pagination-progressbar .swiper-pagination-progressbar-fill {
  background: #bc4e38;
}

.swiper-container {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;

  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
</style>